input {
  display: block;
  padding: 10px;
  width: 300px;
  margin: 5px 0;
}
input[type="submit"] {
  margin: 0 10px;
}
.form-main-first button {
  width: 300px;
  padding: 7px;
  cursor: pointer;
}
select {
  width: 324px;
  padding: 10px;
  font-size: 16px;
  text-transform: capitalize;
}
.form-main-first {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.main-container {
  display: flex;
  flex-wrap: wrap;
}

.display-card {
  border: 1px solid rgb(228, 223, 223);
  padding: 10px;
  margin: 10px auto;
  width: 400px;
  height: auto;
}

.delete-btn {
  padding: 10px 0;
  display: flex;
  justify-content: space-between;
}

.delete-btn button {
  display: block;
  padding: 7px 20px;
  cursor: pointer;
  background: rgb(221, 85, 85);
  color: white;
  border: none;
}

.delete-btn button:hover {
  box-shadow: 0 0 20px red;
}

.issue-btn {
  text-align: center;
  padding: 30px 0;
}

.issue-btn button {
  margin: 0 5px;
  cursor: pointer;
  color: white;
  border: none;
  padding: 7px 20px;
  border-radius: 2px;
  text-transform: capitalize;
  letter-spacing: 1.3px;
}

.delete-btn button:nth-child(even) {
  background: rgb(95, 226, 95);
}
.delete-btn button:nth-child(even):hover {
  box-shadow: 0 0 20px green;
}
